@import "https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serrif;
}

body {
  background-image: url("Wallpaper 1.c6ed7790.jpg");
  background-position: center;
  background-size: cover;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.wrapper {
  backdrop-filter: blur(10px);
  color: #fff;
  background: none;
  border: 2px solid #fff3;
  border-radius: 12px;
  width: 420px;
  padding: 30px 40px;
}

.wrapper h1 {
  text-align: left;
  font-size: 36px;
}

.top-left-heading {
  margin: 20px;
  font-size: 2rem;
  font-weight: bold;
  position: absolute;
  top: 0;
  left: 0;
}

.wrapper .input-box {
  width: 100%;
  height: 50px;
  margin: 30px 0;
  position: relative;
}

.input-box input {
  color: #fff;
  background: none;
  border: 2px solid #fff3;
  border-radius: 40px;
  outline: none;
  width: 100%;
  height: 100%;
  padding: 20px;
  font-size: 16px;
}

.input-box input::placeholder {
  color: #fff;
}

.input-box i {
  font-size: 20px;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.wrapper .remember-forget {
  justify-content: space-between;
  margin: -15px 0 15px;
  font-size: 14.5px;
  display: flex;
}

.remember-forget label input {
  accent-color: #fff;
  margin-right: 3px;
}

.remember-forget a {
  color: #fff;
  text-decoration: none;
}

.remember-forget a:hover {
  text-decoration: underline;
}

.wrapper .btn {
  cursor: pointer;
  color: #333;
  background: #fff;
  border: none;
  border-radius: 40px;
  outline: none;
  width: 100%;
  height: 45px;
  font-size: 16px;
  font-weight: 600;
  box-shadow: 0 0 10px #0000001a;
}

.wrapper .register-link {
  text-align: center;
  margin: 20px 0 15px;
  font-size: 14.5px;
}

.register-link p a {
  color: #fff;
  font-weight: 600;
  text-decoration: none;
}

.register-link p a:hover {
  text-decoration: underline;
}
